body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('Pokemon.jpg');
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Rock {
  background-color: rgb(148, 81, 81);
}

.Grass {
  background-color: #E2F9E1;
}

.Ghost {
  background-color: rgb(247, 247, 247);
}
.Ground{
  background-color: rgba(173, 155, 50, 0.863);

}

.Electric {
  background-color: rgb(255, 255, 161);
}

.Bug {
  background-color: #F6D6A7;
}

.Poison {
  background-color: #e0a7f6;
}

.Normal {
  background-color: #F4F4F4;
}

.Fairy {
  background-color: rgba(255, 192, 203, 0.863);
}

.Fighting{
  background-color: rgba(70, 68, 60, 0.863);
}

.Fire {
  background-color: #FBE3DF;
}

.Psychic{
  background-color: #ad25a7;
}

.Water {
  background-color: #E0F1FD;
}

.Ice{
  background-color: #2398eb;

}

.Dragon{
  background-color: #13ad2d;

}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 3rem 0.5rem;
}

.pokemon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.all-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.thumb-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1.5rem 0;
  margin: 0.3rem;
  border: 1px solid #efefef;
  border-radius: 0.2rem;
  min-width: 160px;
  text-align: center;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.089);
}

h3 {
  margin-bottom: 0.2rem;
}

.thumb-container .number {
  border-radius: 1rem;
  padding: 0.2rem 0.4rem;
  background-color: rgba(196, 170, 27, 0.3);
}

.thumb-container img {
  width: 120px;
  height: 120px;
}

.thumb-container small {
  text-transform: capitalize;
}

.detail-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}



.detail-wrapper button {
  color: rgb(22, 22, 22);
  padding: .5rem;
  margin-top: 1rem;
  border: none;
  border-radius: 0.2rem;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.185);
}

.load-more {
  background: #f5f5f5 linear-gradient(#f5f5f5, #f1f1f1) repeat-x;
  border-radius: 2px;
  border: 1px solid #c6c6c6;
  color: #444;
  padding: 0.5rem 1.5rem;
  min-width: 50%;
  margin-top: 1rem;
}

.load-more:hover{
  background:rgb(212, 207, 207);
  cursor: cell;
}